import { Button, Input } from "antd";
import React, { useState } from "react";
import { useContext } from "react";
import { CrudContext } from "../../../context/CrudContext";
import { notificacion } from "../../../helpers/mensajes";
import MainModal from "../../modal/MainModal";

const ObservacionAprobacion = ({
  modal,
  setModal,
  getPlanilla,
  data,
  observacion,
  boton,
  actualizarTabla
}) => {
  const { updateData } = useContext(CrudContext);
  const [info, setInfo] = useState("");

  const closeModal = () => {
    setModal(false);
  };

  const onChange = (e) => {
    setInfo(e.target.value);
  };

  const hadleSubmit = async () => {
    let observacion = {
      observaciones: info,
    };

    const response = await updateData(
      observacion,
      data.aprobacion_id ?? data.id,
      "aprobacion/observacion"
    );

    if (response) {
      notificacion(response.status, response.msg);
      closeModal();
      actualizarTabla()
    }
  };
  return (
    <MainModal
      className={"modal-usuario"}
      title={boton ? "Agregar Observación" : "Observación RRHH"}
      open={modal}
      width={400}
      closeModal={closeModal}
    >
      <Input.TextArea
        showCount
        maxLength={255}
        defaultValue={ data?.observaciones ? data?.observaciones :observacion ? observacion : ""}
        style={{ height: 120, marginBottom: 24, marginTop: 15 }}
        onChange={onChange}
        placeholder="Observación"
      ></Input.TextArea>
      <div
        style={{
          width: "100%",
          display: "flex",
          justifyContent: "flex-end",
          paddingTop: "10px",
        }}
      >
        {boton && <Button onClick={hadleSubmit}>Guardar</Button>}
      </div>
    </MainModal>
  );
};

export default ObservacionAprobacion;
